<template>
  <div id="particles">
    <van-nav-bar fixed title="UID速通">
      <template #left>
        <van-icon
            @click="backSpace"
            size="0.4rem"
            name="arrow-left"
            color="#333"
        />
      </template>
    </van-nav-bar>

    <div class="page_header">
      <van-notice-bar class="notice" mode="closeable">UID模式的失败率比较高，建议使用抓包模式。</van-notice-bar>
      <van-form @submit="onSubmit">
        <van-field
            v-model="uid"
            type="digit"
            name="uid"
            label="UID"
            placeholder="请输入你的UID"
            right-icon="question-o"
            @click-right-icon="getUid"
            :rules="[{ required: true, message: '请填写你的UID' }]"
        />
        <van-field name="passCount" label="通关次数">
          <template #input>
            <van-stepper
                min="1"
                max="1"
                v-model="passCount"/>
          </template>
        </van-field>

        <van-tag v-if="showTag" closeable plain size="medium" type="success" @close="showTag = false">
          首页右上方'设置'中可获取到自己的UID。
        </van-tag>
        <div style="margin: 16px;">
          <van-button :loading="loading" round block type="info" loading-type="spinner" loading-text="请稍候" native-type="submit">提交</van-button>
        </div>
      </van-form>

      <div class="result-msg" v-if="result.length > 0">{{ result }}</div>

    </div>

    <van-popup v-model="show" round>
      <van-image :src="require('@/assets/img/uid.png')"/>
    </van-popup>

  </div>
</template>

<script>
import {Toast} from 'vant';
import {request} from "@/assets/js/request";
import {NavBar, Icon, Form, Field, Button, Stepper, Popup, Image, Tag, NoticeBar} from 'vant'

export default {
  name: 'template',
  components: {
    [NavBar.name]: NavBar,
    [Icon.name]: Icon,
    [Form.name]: Form,
    [Field.name]: Field,
    [Button.name]: Button,
    [Stepper.name]: Stepper,
    [Popup.name]: Popup,
    [Image.name]: Image,
    [Tag.name]: Tag,
    [NoticeBar.name]: NoticeBar,
  },
  props: {},
  data() {
    return {
      result: "",
      loading: false,
      uid: null,
      passCount: 1,
      show: false,
      showTag: true,
      query: {}
    }
  },
  computed: {},
  watch: {},
  created() {
  },
  mounted() {
    this.query = this.$route.query
  },
  methods: {
    getUid() {
      this.show = true
    },
    onSubmit(values) {
      this.result = ""
      this.loading = true

      const toast = Toast.loading({
        duration: 0,
        forbidClick: true,
        message: '处理中 120 秒',
      });
      let second = 120;
      const timer = setInterval(() => {
        second--;
        if (second) {
          toast.message = `处理中 ${second} 秒`
        } else {
          Toast.clear()
          clearInterval(timer)
          this.loading = false
        }
      }, 1000)


      request({
        url: '/app/basics/yyy/play/uid',
        method: 'post',
        data: values
      }).then((response) => {
        Toast.clear()
        clearInterval(timer)
        this.loading = false
        this.result = response.data

      }).catch((error) => {

        Toast.clear()
        clearInterval(timer)
        this.loading = false
        this.result = error.toString()
      })
    }
  }
}
</script>

<style lang="css" scoped>

.notice {
  margin-bottom: 0.3rem;
}

.result-msg {
  white-space: pre-line;
  text-align: left;

  background: #282C34;
  color: #97C178;
  font-size: 10px;
  border-radius: 7px;
  padding: 10px;
  margin-top: 0.65rem;
}

</style>
